.col-item{
    width: 80px;
    height: 80px;
    background: none;
    border:1px solid #ccc;
    font-size: 28px;
    /*
    因为父级设置了flex布局,当宽度超出屏幕时,
    子元素宽度不生效,子元素需要设置此属性flex-shrink,0代表不会收缩
    或者使用min-width代替width
    */
    flex-shrink:0;
}
.row-item{
    display: flex;
    flex-wrap: nowrap;
}
.content{
    margin-left: 30px;
}
.guige{
    margin-left: 30px;
}
.right{
    background-color: #7bed9f;
}
.wrong{
    background-color: #ff6b81;
}